<template>
    <div>
        <TopLink></TopLink>
        <HomeBtn v-bind:msg="arr"></HomeBtn>
        <ListItems :listmsg="listdata"></ListItems>
    </div>
</template>
<script>
import TopLink from "@/components/toplink/toplink.vue"
import HomeBtn from "@/components/homebtn/homebtn.vue"
import ListItems from "@/components/listitems/listitems.vue"
import axios from "axios"
export default {
    components:{
        TopLink,HomeBtn,ListItems
    },
    data(){
        return {
            arr:[
                {title:"影院热映",path:"/"},
                {title:"欧美新碟榜",path:"/"},
                {title:"注册豆瓣",path:"/zhuce"},
                {title:"登录豆瓣",path:"/login"},
            ],
            listdata:[],
        }
    },
    mounted() {
        axios({
            url:"/home/listitems",
            method:"get"
        }).then(res=>{
            this.listdata = res.data.shouye
        })
    },
}
</script>
